.theme-wrapper[data-theme='light'] {
  --border: #363739;
  --text-default: #363739;
  --container-back:  #f7f6f2;
  --container-mid: #dddcd9;
  --container-fore: #eeedea;
  --container-highlight: #edece9;
  --container-shadow: #e3e1dd;
  --fill-color: #547a20;
}

.theme-wrapper[data-theme='dark'] {
  --border: #667069;
  --text-default: #d0c2a4;
  --container-back: #232a2f;
  --container-mid: #2b3339;
  --container-highlight:#3b4349;
  --container-fore: #313a3f;
  --container-shadow: #131a1f;
  --fill-color: #dbbc7a;
}

html {
  font-family: arial;
  background-color: var(--container-back);
}

#portrait-overlay {
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: var(--container-back);
  z-index: 100;
}

#overlay-text-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
}

.theme-wrapper {
  text-align: center;
  color: var(--text-default);
  font-family: 'montserrat';
  background-color: var(--container-back);
}

