.page-header {
  background-color: var(--container-back);
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  /* font-size: calc(10px + 2vmin); */
}

.App-link {
  color: #61dafb;
}

.rotate-icon {
  margin: 1em;
}

#theme-icon-wrapper {
  position: absolute;
  margin: 1em;
  top: 0;
  right: 0;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

#audio-player {
  width: 80vw;
  background-color: none;
  margin-top: .5em;
}

.title {
  font-size: 1.5em;
  letter-spacing: .5em;
}